
import Vue from 'vue'
import App from './App.vue'
import 'normalize.css';
import './assets/css/reset.css'
import './assets/css/layer.css'
import './assets/js/layer'
import './libs/touch'
import animate from 'animate.css'
import VueRouter from 'vue-router'
Vue.use(animate)
Vue.use(VueRouter)
Vue.config.productionTip = false
Vue.prototype.$layer= window.layer
const router = new VueRouter({
  routes:[
    {
      path:'/',name:'niunui',component:()=>import(/* webpackChunkName: "home" */ '../src/components/home.vue')
    },
    {
      path:'/yaoyao', name :'yaoyao', component:()=>import(/* webpackChunkName: "yaoyao" */ '@/components/yaoyaole.vue')
    }
  ]
})

new Vue({  
  render: h => h(App),
  router,
}).$mount('#app')

